@import "./variables";
@import "./functions";

@mixin mdc-shape-radius($radius, $rtl-reflexive: false) {
  border-radius: mdc-shape-prop-value_($radius);

  @if ($rtl-reflexive) {
    @include mdc-rtl {
      border-radius: mdc-shape-flip-radius($radius);
    }
  }
}
